body {
  margin: 0;
  font-family: "Verdana", "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}

#root {
  display: flex;
  height: 100vh;
  justify-content: center;
}

#editor {
  margin-top: 20vh;
  margin-bottom: 10vh;
}

code {
  white-space: pre-wrap;
}

img {
  width: 20%;
  height: 20%;
}
